import React from 'react';
import './SingleState.css';
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

class SingleState extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            stateName: '',
            confirm:0,
            recovered:0,
            active:0,
            deceased:0
         }
    }
    componentDidMount(){
        console.log('State to Render...',this.props.stateData)
        this.setState({ stateName: this.props.stateData.state}) 
    }
    GoTo(){
        this.props.history.push('/')
    }
    render() {
        return ( 
            <React.Fragment>
                <div className={(this.props.darkmode)? 'dark_bg' : ''}>
                <Grid container className={(this.props.darkmode)? 'dark_bg' : ''}>
                    <Grid item xs={12} sm={12} md={8} lg={12} xl={12}>
        <Typography variant="subtitle1" className={(this.props.darkmode)?'dark_HomeRoute':'HomeRoute'} gutterBottom onClick={this.GoTo.bind(this)}>Home <big className='dark_strock'>/</big><span className='statename'>{this.state.stateName}</span></Typography>
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item xs={12} sm={12} md={8} lg={12} xl={12} className='head_stateName_parent'>
                    <Typography variant="subtitle1" className='head_stateName' gutterBottom>{this.state.stateName}</Typography>
                    </Grid>
                </Grid>
                </div>
            </React.Fragment>
         );
    }
}
const mapStateToProps = (state) =>{
    return {
        darkmode : state.response.darkmode,
        stateData: state.steteresponse.singlestate
    }
}

export default connect(mapStateToProps)(withRouter(SingleState));