import { SINGLE_STATE } from '../Actions/type.js';

const initialState = {
    singlestate:[]
}

export default function(state = initialState, action){
    console.log('State Details...',initialState)
    switch(action.type){
        case SINGLE_STATE:
            return {
                ...state,
                singlestate:{...action.payload}
            }
        default:
            return state;
    }
}