import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import './App.css';
import NavBar  from '../src/Components/NavBar/NavBar.js';
import { makeStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import SingleState from '../src/Components/SingleState/SingleState.js';


const useStyles = makeStyles((theme) => ({
  darkapp:{
    background : '#161625 !important'
  }
}))

const App = (props) => {
  const classes = useStyles();
  return (
    <BrowserRouter>
      <div className={(props.darkmode)?classes.darkapp : 'App'}>
        <NavBar />
        <Route exact path='/state/:state' component={SingleState}></Route>
      </div>
   </BrowserRouter>
  );
}

const mapStateToProps = (state) =>{
  return {
      darkmode : state.response.darkmode
  }
}
export default connect(mapStateToProps)(App);
